import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { UserProfileModel } from '@core/models/user-profile.model';
import { ApiService } from '@core/services/api.service';
import { ToastService } from '@core/services/toast.service';
import { faCalendarAlt } from '@fortawesome/free-regular-svg-icons';
import { NgbCalendar, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CreditsApi, UsersApi, ValuesApi } from '@shared/constants/api';
import { EmailValidator } from '@shared/validators';
import * as moment from 'moment';
import { BehaviorSubject, NEVER, Observable, of, Subject } from 'rxjs';
import { catchError, concatMap, map, takeUntil, tap } from 'rxjs/operators';

import { CountryModel } from './models/country.model';
import { CreditHistoryRecord } from './models/user-credit-history-record.model';
import { UserCreditsModel } from './models/user-credits.model';
import {
  DeleteConfirmModalComponent,
} from './delete-confirm-modal/delete-confirm-modal.component';

@Component({
  selector: 'duelit-user-edit',
  templateUrl: './user-edit.component.html',
  styleUrls: ['./user-edit.component.scss']
})
export class UserEditComponent implements OnInit, OnDestroy {
  readonly creditHistoryLimit = 15;

  user$: Observable<UserProfileModel>;
  userCredits$: Observable<UserCreditsModel>;
  userCreditsHistory$: BehaviorSubject<CreditHistoryRecord[]> = new BehaviorSubject<CreditHistoryRecord[]>([]);
  countries$: Observable<string[]>;
  states$: Observable<string[]>;
  userDetailsForm: FormGroup;
  userCreditsControl: FormControl;
  calendarIcon = faCalendarAlt;
  isSavingUser = false;
  isCreditsUpdating = false;
  isDeleting = false;
  isUnregistering = false;
  creditHistoryPage$ = new BehaviorSubject<number>(1);
  lastCreditHistoryCount = this.creditHistoryLimit;
  ngUnsubscribe$ = new Subject();

  constructor(
    private route: ActivatedRoute,
    private api: ApiService,
    private fb: FormBuilder,
    private calendar: NgbCalendar,
    private toast: ToastService,
    private router: Router,
    private modalService: NgbModal,
  ) { }

  ngOnInit(): void {
    const userId = this.route.snapshot.params.userId;

    const path = this.api.buildPath(UsersApi.GetUserById, [userId]);
    this.user$ = this.api.get(path).pipe(
      tap((user: UserProfileModel) => {
        const _bday = moment(user.birthDate).toObject();
        const bdayCal = this.calendar.getToday();
        bdayCal.day = _bday.date;
        bdayCal.month = _bday.months + 1;
        bdayCal.year = _bday.years;

        this.userDetailsForm = this.fb.group({
          firstName: new FormControl(user.firstName),
          lastName: new FormControl(user.lastName),
          email: new FormControl(user.email, [EmailValidator, Validators.required]),
          country: new FormControl(user.country),
          state: new FormControl(user.state),
          birthDate: new FormControl(bdayCal),
          status: new FormControl(user.status),
          role: new FormControl(user.role),
          banDescription: new FormControl(user.banDescription)
        });
      }),
      catchError(err => {
        this.toast.error(err.error || err.statusText || err.message);
        this.router.navigate(['/admin/users']);
        return NEVER;
      })
    );

    const userCreditsPath = this.api.buildPath(CreditsApi.CreditsByUser, [userId]);
    this.userCredits$ = this.api.get(userCreditsPath) as Observable<UserCreditsModel>;

    this.initCreditHistory(userId);

    this.countries$ = this.api.get(ValuesApi.Countries).pipe(
      map((countries: CountryModel[] = []) => countries.map(country => country.name))
    );
    this.states$ = this.api.get(ValuesApi.States).pipe(
      map((countries: CountryModel[] = []) => countries.map(country => country.name))
    );
    this.userCreditsControl = new FormControl(0);
  }

  ngOnDestroy() {
    this.ngUnsubscribe$.next();
    this.ngUnsubscribe$.complete();
  }

  initCreditHistory(userId) {
    const userCreditsHistoryPath = this.api.buildPath(CreditsApi.CreditsHistoryByUser, [userId]);

    this.creditHistoryPage$.pipe(
      concatMap(page => {
        return this.api.get(userCreditsHistoryPath, {
          query: {
            page,
            limit: this.creditHistoryLimit
          }
        }).pipe(
          tap((result: CreditHistoryRecord[]) => {
            this.lastCreditHistoryCount = result.length;
          }),
          catchError(err => {
            this.toast.error(err.error || err.statusText || err.message);
            return NEVER;
          })
        ) as Observable<CreditHistoryRecord[]>;
      }),
      takeUntil(this.ngUnsubscribe$)
    ).subscribe(history => {
      this.userCreditsHistory$.next(this.userCreditsHistory$.value.concat(history));
    });
  }

  updateUser() {
    const formValue = this.userDetailsForm.value;
    const birthday = moment().year(formValue.birthDate.year).month(formValue.birthDate.month - 1)
      .date(formValue.birthDate.day).format('YYYY-MM-DD');
    const newUserValue = {
      first_name: formValue.firstName,
      last_name: formValue.lastName,
      email: formValue.email,
      country: formValue.country,
      state: formValue.state,
      role: formValue.role,
      status: formValue.status,
      banDescription: formValue.status === 'banned' ? formValue.banDescription : '',
      birth_date: birthday
    };

    this.isSavingUser = true;
    this.api.post(UsersApi.UpdateUser, {
      body: newUserValue,
      opts: {
        addSecurity: false
      }
    }).pipe(
      tap(() => this.isSavingUser = false),
      catchError(err => {
        this.isSavingUser = false;
        this.toast.error(err.error || err.statusText || err.message);
        return NEVER;
      })
    ).subscribe(() => {
      this.toast.success('Successfully updated user details.');
    });
  }

  updateCredits() {
    this.isCreditsUpdating = true;

    const userId = this.route.snapshot.params.userId;
    const creditsToAdd = this.userCreditsControl.value;
    this.userCreditsControl.disable();

    const path = this.api.buildPath(CreditsApi.CreditsByUser, [userId]);
    this.api.post(path, {
      body: {
        virtual_amount: creditsToAdd
      },
      opts: {
        addSecurity: false
      }
    }).pipe(
      tap(() => {
        this.isCreditsUpdating = false;
        this.userCreditsControl.enable();
        this.userCreditsControl.setValue(0);
      }),
      catchError(err => {
        this.isCreditsUpdating = false;
        this.userCreditsControl.enable();
        this.toast.error(err.error || err.statusText || err.message);
        return NEVER;
      })
    ).subscribe((data: UserCreditsModel) => {
      this.userCredits$ = of(data);
      this.reloadCreditHistory();
      this.toast.success('Successfully updated user credits.');
    });
  }

  unregisterDevice() {
    this.isUnregistering = true;

    this.api.post(UsersApi.UnregisterDevice, {
      body: {
        email: this.userDetailsForm.value.email
      },
      opts: {
        addSecurity: false
      }
    }).pipe(
      tap(() => this.isUnregistering = false),
      catchError(err => {
        this.isUnregistering = false;
        console.log("ERROR in unregistering", err);
        this.toast.error(err.error || err.statusText || err.message);
        return NEVER;
      })
    ).subscribe(() => {
      this.toast.success('Successfully unregistered device.');
    });
  }

  openUserStats() {
    const userId = this.route.snapshot.params.userId;

    this.router.navigate([`/admin/user`, userId, 'stats']);
  }

  confirmDelete() {
    const deleteModalRef = this.modalService.open(DeleteConfirmModalComponent);

    deleteModalRef.result.then((action) => {
      if (action === 'ok') {
        this.deleteUser();
      }
    }).catch(() => {
      return;
    });
  }

  deleteUser() {
    const userId = this.route.snapshot.params.userId;
    this.isDeleting = true;

    const path = this.api.buildPath(UsersApi.DeleteUserById, [userId]);
    this.api.delete(path).pipe(
      catchError(err => {
        if (err.status === 200) {
          return of({});
        } else {
          this.toast.error(err.error || err.statusText || err.message);
          this.isDeleting = false;
          return NEVER;
        }
      })
    ).subscribe(() => {
      this.toast.success(`Successfully deleted user ${this.userDetailsForm.value.email}`);
      this.router.navigate(['/admin/users']);
    });
  }

  loadMoreCreditHistory() {
    this.creditHistoryPage$.next(this.creditHistoryPage$.value + 1);
  }

  reloadCreditHistory() {
    this.userCreditsHistory$.next([]);
    this.creditHistoryPage$.next(1);
  }
}
